import React from 'react';


const image_url1 = 'data:image/jpeg;base64,'

const image_url2 = 'data:image/jpeg;base64,'

const image_url3 = 'data:image/jpeg;base64,'

const image_url4 = 'data:image/jpeg;base64,'

const renderSong = (cover: string, name: string, artist: string) => {

    const min = (a: number, b: number) => {
        return a < b  ? a : b;
    }

    return (
        <div
            className='song'
        >
            <img 
                src={cover}
                alt={name}
                height={min(window.innerHeight, window.innerWidth)/4}
                width={min(window.innerHeight, window.innerWidth)/4}
            />
            <p className='song-title'>{name}</p>
            <p className='artist-title'>{artist}</p>
        </div>
    )
}

interface SongListProps {
    songListTitle: string
}


const SongList: React.FC<SongListProps> = ({
    songListTitle
}) => {
    return (
        <div
            className='song-list-container'
        >
            <div className='song-list-title'>
                <h3>    
                    {songListTitle}
                </h3>   
            </div>

            <div className='song-list'>
                {renderSong(image_url1, 'Circles', 'Post Malone')}
                {renderSong(image_url2, 'Peaches (feat. Daniel Caesar)', 'Justin Bieber')}
                {renderSong(image_url3, 'deja vu', 'Olivia Rodrigo')}
                {renderSong(image_url4, 'Your Power', 'Billie Eilish')}
            </div>

        </div>
    )
}

export default SongList;